@import url(https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-regular-webfont.woff);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.loginComponent {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  background: url(/ticket/static/media/background.abeaa0ae.png) no-repeat center center fixed;
  background-size: cover cover;
}
.loginComponentBlur {
  background: rgba(255, 255, 255, 0.6);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loginBox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 410px;
  height: 338px;
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
}

.loginHeading {
  width: 100%;
  position: absolute;
  top: 53px;
  margin: 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  align-items: center;
  text-align: center;
  color: #424242;
}
.loginButton {
  position: absolute;
  bottom: 37px;
  right: 33px;
  width: 116px;
  height: 34px;
  background: linear-gradient(104.34deg, #3c4cad -15.34%, #00c3ff 145.95%);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
  border: 0;
  border-radius: 4px;

  /**/
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  align-items: center;
  text-align: center;

  color: #ffffff;
}
.loginButton:hover {
  box-shadow: 0px 0px 4px #99a0a3;
}
.loginButton:active {
  background: linear-gradient(104.34deg, #5362be -15.34%, #4ad5ff 145.95%);
  border: 1.5px solid rgba(29, 138, 216, 0.3);
}
.usernameHeading,
.passwordHeading {
  position: absolute;
  width: 107px;
  height: 14px;
  left: 33px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  color: #424242;
  margin: 0;
}
.usernameHeading {
  top: 107px;
}
.passwordHeading {
  top: 187px;
}
.usernameInput,
.passwordInput {
  position: absolute;
  margin: 0;
  left: 33px;
}
.usernameInput {
  top: 128px;
  width: 345px;
  height: 34px;
  border: 1px solid #c9cacc;
  box-sizing: border-box;
  border-radius: 4px;
}
.passwordInput {
  top: 208px;
  width: 345px;
  height: 34px;
  border: 1px solid #c9cacc;
  box-sizing: border-box;
  border-radius: 4px;
}
.loginErrorInput {
  border: 1px solid rgba(235, 23, 23, 1);
}
.loginErrorHeading {
  color: rgba(235, 23, 23, 1);
}
.usernameError,
.passwordError {
  position: absolute;
  left: 33px;
  height: 13px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  align-items: center;
  color: red;
  margin: 0;
}
.usernameError {
  top: 165px;
}
.passwordError {
  top: 244px;
}

