@-moz-document url-prefix() {
  .departureCalendar {
    border: 2px #424242 solid;
    border-radius: 3px;
  }
}
.departureCalendar::-webkit-calendar-picker-indicator {
  background: center / contain no-repeat url(data:image/png;base64,);
  height: 18px;
  width: 20px;
}
.photoCarousel {
  width: 600px;
  height: 149px;
  position: absolute;
  top: 127px;
  left: 40px;
  overflow: hidden;
  white-space: nowrap;
  word-wrap: normal;
}
.photo {
  height: 149px;
  width: 164px;
  box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}
.photoContainer {
  height: 149px;
  width: 164px;
}
.photoBlur {
  position: absolute;
  right: 0;
  top: 0;
  height: 149px;
  width: 10px;
  background: linear-gradient(
    270deg,
    rgba(255, 255, 255, 0.8) 0%,
    rgba(255, 255, 255, 0) 83.85%
  );
  z-index: 10000;
}
.favorites {
  position: absolute;
  width: 300px;
  height: 21px;
  left: 50px;
  top: 307px;

  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 17px;
  line-height: 22px;
  /* or 129% */

  letter-spacing: -0.408px;
  margin: 0;
  color: #424242;
}
.favoritesCount {
  color: #1157a7;
}
.ticketList {
  position: absolute;
  left: 47px;
  top: 340px;
  height: 515px;
  width: 600px;
  display: flex;
  flex-direction: column;
  overflow: auto;
  overflow-x: hidden;

  scrollbar-color: #1157a7 #e7e7e7;
  scrollbar-width: thin;
}
::-webkit-scrollbar-button {
  background-image: url("");
  background-repeat: no-repeat;
  width: 2px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: #e7e7e7;
  border-radius: 2px;
  width: 2px;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #1157a7;
}

::-webkit-resizer {
  background-image: url("");
  background-repeat: no-repeat;
  width: 24px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 2px;
}
